<template>
  <div>{{team_1_name}}</div>
  <div>{{team_1_count}}</div>

  <Doughnut
    :chart-options="chartOptions"
    :chart-data="chartData"
    :chart-id="chartId"
    :dataset-id-key="datasetIdKey"
    :plugins="plugins"
    :css-classes="cssClasses"
    :styles="styles"
    :width="width"
    :height="height"
  />
</template>

<script>
import { Doughnut } from 'vue-chartjs'
import { Chart as ChartJS, Title, Tooltip, Legend, ArcElement, CategoryScale } from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, ArcElement, CategoryScale)

export default {
  name: 'DoughnutChart',
  components: { Doughnut },
  props: {
    chartId: {
      type: String,
      default: 'doughnut-chart'
    },
    datasetIdKey: {
      type: String,
      default: 'label'
    },
    width: {
      type: Number,
      default: 400
    },
    height: {
      type: Number,
      default: 400
    },
    cssClasses: {
      default: '',
      type: String
    },
    styles: {
      type: Object,
      default: () => {}
    },
    plugins: {
      type: Object,
      default: () => {}
    },
    stat : {
      type: Object
    }
  },
  data() {
    return {
      // chartData: {
      //   labels: [1,2,3,4,5,6],
      //   datasets: [ { backgroundColor: ['#41B883', '#E46651', '#00D8FF','#41B883', '#E46651', '#00D8FF', ], data: [1,2,3,4,5,6] } ]
      // },
      // chartOptions: {
      //   responsive: true
      // },
      team_1_name : this.stat[0].name,
      team_1_count :  this.stat[0].count,
      team_2_name : this.stat[1].name,
      team_2_count :  this.stat[1].count,
      team_3_name : this.stat[2].name,
      team_3_count :  this.stat[2].count,
      team_4_name : this.stat[3].name,
      team_4_count :  this.stat[3].count,
      team_5_name : this.stat[4].name,
      team_5_count :  this.stat[4].count,
      team_6_name : this.stat[5].name,
      team_7_count :  this.stat[5].count,
      teamName : [this.stat[0].name, this.stat[1].name, this.stat[2].name, this.stat[3].name, this.stat[4].name, this.stat[5].name,],
      teamCount : [this.stat[0].count, this.stat[1].count, this.stat[2].count, this.stat[3].count, this.stat[4].count, this.stat[5].count,]
      
    }
  },
  computed : {
    chartData(){
      return{
        labels: this.teamName,
        datasets: [ { backgroundColor: ['#41B883', '#E46651', '#00D8FF','#41B883', '#E46651', '#00D8FF', ], data: this.teamCount } ]
      }
    },
    chartOptions(){
      return{
        responsive: true
      }
    }
  }
}
</script>

